.score-btn{
    margin: 0;
}

.minus{
    background: #F00000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #DC281E, #F00000);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #DC281E, #F00000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.minus:hover{
    background: #FF416C;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #FF4B2B, #FF416C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */    
}

.plus{
    background: #56ab2f;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #a8e063, #56ab2f);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #a8e063, #56ab2f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.plus:hover{
    background: #76b852;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #8DC26F, #76b852);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #8DC26F, #76b852); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */    
}

.my-review-container{
    border-top-style: solid;
    border-bottom-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding-top: 15px;
    padding-bottom: 15px;
}

.score-grid{
    display: grid;
    grid-template-columns: 80% 20%;
}

.score-item{
    padding: 5px;
}

textarea{
    background-color: rgb(102, 95, 95);
    color: white;
    border-radius: 4px;
    outline-style: none;
    outline-color: red;
}

textarea:focus{
    outline-color: rgb(9, 255, 0);
}

.review-grid{
    display: grid;
    grid-template-columns: auto auto auto;
}

.review-grid p {
    margin-top: 0px;
    margin-left: auto;
}

.review-grid div{
    margin-left: auto;
}

.nick-name{
    font-size: 25px;
    margin-bottom: auto;
}

.review-grid img{
    min-height: 10px;
    max-height: 35px;
    max-width: 80px;
}

.review-container{
    border-top-style: solid;
    border-bottom-style: solid;
    border-width: 1px;
    border-radius: 10px;
    padding: 10px;
    margin: 10px;
}

.review-nick-name{
    text-align: left;
}

.review-date{
    font-style: italic;
}

.review-grid button{
    margin: auto;
    width: 100px;
    height: 30px;
    margin-top: 0;
}

.up-arrow{
    font-size: 20px;
}

.up-arrow:hover{
    color: #66ff66;
    cursor: pointer;
}

.down-arrow{
    font-size: 22px;
}

.down-arrow:hover{
    color:#FF6666;
    cursor: pointer;
}

.pop-index{
    font-size: 20px;
    margin: 0 10px;
}

.clickable{
    color: rgb(202, 67, 50);
    font-size: 18px;
  }
  
.clickable:hover{
    color: white;
    cursor: pointer;
}

.review-text{
    text-align: justify;
    padding-left: 1%;
    padding-right: 1%;
}

.followed-review-grid{
    display: grid;
    grid-template-columns: 20% 80%;
}

.review-poster-container{ 
    padding: 5px;
    padding-top: 20px;
    border-radius: 5px;
    color: white;
}

.review-poster-container:hover{ 
    background-color: rgba(255, 255, 255, 0.3);
}

.followed-review-grid img{
    width: 100%;
    border-radius: 10px;
}

.review-poster-container p{
    text-align: center;
    font-size: 18px;
}