.topnav {
    overflow: hidden;
    background: #04091e; 
    z-index: 9999;
  }

  .topnav a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 20px;
    font-weight: bolder;
  }

  .topnav a {
    color: #f2f2f2;
      text-decoration: none;
      font-size: 20px;
      font-weight: bolder;
  }

  .fa-sign-out-alt{
    float: right;
    color: #b41e1e;
    font-size: 24px;
    text-align: center;
    padding: 14px 16px;
  }

  .topnav a:hover {
    background: #e0eafc; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #e0eafc, #cfdef3); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #e0eafc, #cfdef3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: black;
  }
  
  .topnav a.active {
    background: #f6214b; /* fallback for old browsers */
     color: white;
  }
  
  .topnav .icon {
    display: none;
  }
  
  @media screen and (max-width: 600px) {
    .topnav a:not(:first-child) {display: none;}
    .topnav a.icon {
      float: right;
      display: block;
    }
  }
  
  @media screen and (max-width: 600px) {
    .topnav.responsive {position: relative;}
    .topnav.responsive a.icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    .topnav.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }
  
  .sticky {
    position: fixed;
    top: 0;
    width: 100%;
  }

  .logged{
    background: #000000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #0f9b0f, #000000);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #0f9b0f, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  }