.edit-cast-row{
    display: grid;
    grid-template-columns: 10% 20% 25% 25% 10% 10%;
    min-height: 50px;
    max-height: 150px;
    font-size: 15px;
    padding: 5px;
    padding-bottom: 10px;
    padding-top: 10px;
    margin: 0px;
}

.edit-cast-row p{
    font-size: 17px;
}

.edit-cast-row img{
    height: 100%;
}

.unsaved{
    background: #485563;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #29323c, #485563);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #29323c, #485563); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */    
}

.cast-row{
    display: grid;
    grid-template-columns: 100px auto auto;
    min-height: 100px;
    max-height: 1000px;
    font-size: 15px;
    padding: 5px;
    padding-bottom: 10px;
    padding-top: 10px;
    margin: 0px;
    text-align: left;
}

.cast-row-item{
    text-align: left;
}

.cast-row img{
    height: 100%;
}

.cast-row p{
    font-size: 17px;
    text-align: right;
    margin-top: auto;
    margin-bottom: auto;
}

.cast-row a{
    font-size: 17px;
    margin: auto;
}

.cast-row span{
    font-size: 17px;
    margin-top: auto;
    margin-bottom: auto;
}