.detail-grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}
@media (min-width: 300px) {
    .span-2 {
        grid-column: auto / span 2;
    }
}

.info-grid{
    display: grid;
    grid-template-columns: 35% 65%;
}

.info-grid-item{
    padding: 20px;
}

.info-grid-item p{
    font-size: 18px;
}

.info-grid-item ul{
    margin: 0;
}

.info-grid-item li{
    font-size: 18px;
    list-style-type: none;
}

.detail-grid-item{

}

.title-input{
    font-size: 24px;
    width: 50%;
    border-radius: 3px;
    font-weight: bolder;
}

.file-input{
    width: 80%;
    font-size: 14px;
    padding: 1px;
    margin: auto;
    outline: none;
  }

.poster{
    width: 95%;
    border-style: solid;
    border-width: 4px;
    border-radius: 5px;
    border-color: rgb(134, 155, 214);
}

.edit-genre{
    border-style: solid;
    border-width: 1px;
    border-color: grey;
    margin: 10px;
    padding: 2px;
    border-radius: 5px;
}

.edit-genre span{
    font-size: 22px;
    margin: 5px;
}

.edit-genre button{
    border-style: none;
    float: right;
    margin-left: 4px;
    margin-right: 4px;
    padding: 1px;
    margin-top: 0;
    background: none;
    font-size: 20px;
}

.edit-genre button:hover{
    color: red;
}

.edit-genre:hover{
    background-color: rgba(230, 255, 255, 0.3);
}

.genre{
    color: rgb(156, 156, 156);
    margin: 10px 20px;
    text-decoration: none;
    display: inline-block;
}

.genre:hover{
    color: white;
    text-decoration: underline;
}

.title{
    font-size: 40px;
    margin: 0;
}

.star{
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 15px;
    margin-bottom: 5px;
}

.score{
    margin: 10px;
    font-size: 20px;
    background: #FDC830;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #F37335, #FDC830);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #F37335, #FDC830); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding: 10px;
    border-radius: 50%;
    height: 30px;
    width: 30px;
    position: relative;
    z-index: 0;
}

.main-score{
    margin: 0;
    background: #FDC830;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #F37335, #FDC830);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #F37335, #FDC830); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding: 10px;
    border-radius: 50%;
    height: 60px;
    width: 60px;
    position: relative;
}

.main-score p{
    font-size: 35px;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: bolder;
}

.score p {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

.medium {
    background: #FDC830;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #F37335, #FDC830);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #F37335, #FDC830); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.low{
    background: #f85032;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #e73827, #f85032);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #e73827, #f85032); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */   
}

.high{
    background: #76b852;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #8DC26F, #76b852);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #8DC26F, #76b852); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.score-btn{
    margin: 0;
}

.minus{
    background: #F00000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #DC281E, #F00000);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #DC281E, #F00000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.minus:hover{
    background: #FF416C;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #FF4B2B, #FF416C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */    
}

.plus{
    background: #56ab2f;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #a8e063, #56ab2f);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #a8e063, #56ab2f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.plus:hover{
    background: #76b852;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #8DC26F, #76b852);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #8DC26F, #76b852); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */    
}

.my-review-container{
    border-top-style: solid;
    border-bottom-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding-top: 15px;
    padding-bottom: 15px;
}

.score-grid{
    display: grid;
    grid-template-columns: 80% 20%;
}

.score-item{
    padding: 5px;
}

textarea{
    background-color: rgb(102, 95, 95);
    color: white;
    border-radius: 4px;
    outline-style: none;
    outline-color: red;
}

textarea:focus{
    outline-color: rgb(9, 255, 0);
}

.score-grid{

}

.score-grid .score{
    margin: 10px;
}

.title-grid{
    display: grid;
    grid-template-columns: auto auto;
}

.title-grid p {
    margin-top: auto;
    margin-bottom: auto;
}

.title-grid div{
    margin-left: auto;
}

.movie-grid{
    display: grid;
    grid-template-columns: 33.33% 33.33% 33.33%;
}

.movie-container{
    padding: 10px;
    text-align: center;
    margin: 5px;
    border-bottom-style: solid;
    border-top-style: solid;
    border-radius: 8px;
    border-width: 1px;
}

.movie-container:hover{
    background-color: rgba(176, 217, 229, 0.2);
}

.movie-container img{
    width: 100%;
    border-style: solid;
    border-width: 3px;
    border-radius: 5px;
}

.high-poster{
    border-color: green;
}

.medium-poster{
    border-color: orange;
}

.low-poster{
    border-color: red;
}

.movie-title {
    font-size: 17px;
}

.score-line{
    font-size: 18px;
}

.movie-anchor{
    color: white;
}

.slide-btn{
    background: transparent;
    border-style: none;
    color: rgba(0, 247, 255, 0.8);
    font-size: 14px;
    overflow: hidden;
}

.slide-btn:hover{
    background: transparent;
    background-color: rgba(255, 255, 255, 0.3);
}

.slide-grid{
    display: grid;
    grid-template-columns: 5% 90% 5%;
}