.name-input{
    font-size: 20px;
}

.detail-grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}
@media (min-width: 300px) {
    .span-2 {
        grid-column: auto / span 2;
    }
}

.detail-grid-item{

}

.place-input{
    width: 80%;
}

.file-input{
    width: 80%;
    font-size: 14px;
    padding: 1px;
    margin: auto;
    outline: none;
}

textarea{
    width: 90%;
    margin: auto;
    padding: 2px;
    font-size: 16px;
    font-family: Verdana, Geneva, sans-serif;
}

.portrait-container{
    padding-top: 56.25%; /* 16:9 Aspect Ratio */
    overflow: hidden;
    height: 0;
}

.portrait{
    width: 95%;
    border-style: solid;
    border-width: 3px;
    border-radius: 5px;
    border-color: rgb(134, 155, 214);
}

.artist-row{
    min-height: 50px;
    max-height: 150px;
    font-size: 15px;
    display: grid;
    grid-template-columns: 10% 50% 30%;
    padding: 5px;
    padding-bottom: 10px;
    padding-top: 10px;
    margin: 0px;
    border-style: solid;
    border-width: 1px;
    border-bottom-width: 0.5px;
    border-left-width: 0;
    border-right-width: 0;
    border-collapse: collapse;
}

.artist-row:hover{
    background: #0F2027;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.selected-row{
    background: #870000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #190A05, #870000);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #190A05, #870000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.artist-row img{
    height: 100%;
}

.artist-row p{
    margin: 5%;
    font-size: 18px;
}

.artist-name{
    font-size: 19px;
}

.adder-panel{
    background: rgba(185, 42, 40, 0.6);
    padding: 1%;
    border-radius: 5px;
}

.adder-panel span, input, button{
    margin-left: 1%;
    margin-right: 1%;
}

.adder-panel a{
    text-decoration: none;
    color: white;
    font-style: italic;
    font-size: 18px;
}

.adder-panel a:hover{
    text-decoration: underline;
    color: greenyellow;
}

.info-grid{
    display: grid;
    grid-template-columns: 35% 65%;
}

.info-grid-item{
    padding: 0 20px;
    text-align: justify;
}

.show-more{
    color: rgb(130, 117, 241);
}

.show-more:hover{
    color: white;
    cursor: pointer;
}
