.form-container {
    display: grid;
    grid-template-columns: 50% 50%;
    /*background-color: #2196F3; */
  }
.form-item {
    font-size: 20px;
    padding-left: 20%;
    padding-right: 20%;
    text-align: center;
}

.profile-grid {
  display: grid;
  grid-template-columns: 40% 60%;
  /*background-color: #2196F3; */
}
.profile-grid-item {
  font-size: 20px;
  padding-left: 5%;
  padding-right: 5%;
  text-align: center;
}

.container {
  position: relative;
  width: 100%;
  margin: 10px;
  border-style: solid;
  border-width: 3px;
  border-color: rgb(53, 163, 163);
  border-radius: 5px;;
}

.file-input{
  width: 100%;
  font-size: 14px;
  padding: 1px;
  margin: auto;
  outline: none;
}

textarea{
  resize: none;
  width: 95%;
  outline: 1px solid rgb(53, 163, 163);
  border-radius: 4px;
}


.image {
  display: block;
  width: 100%;
  margin: 2%;
  margin-top: 10px;
  height: auto;
  border-style: solid;
  border-width: 3px;
  border-color: rgb(53, 163, 163);
  border-radius: 5px;;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .3s ease;
  background-color:  rgba(124, 185, 209, 0.4);
}

.container:hover .overlay {
  opacity: 1;
}

.user-grid{
  display: grid;
  grid-template-columns: 60% 40%;
}

.user-grid p {
  padding: 10px;
}

.user-grid img{
  width: 90%;
  max-height: 600px;
  border-style: solid;
  border-width: 2px;
  border-color: gray;
  padding: 1%;
}

.follow{
  background: #e52d27;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #b31217, #e52d27);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #b31217, #e52d27); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
  border-radius: 4px;
}

.follow:hover{
  background: #cb2d3e;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ef473a, #cb2d3e);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ef473a, #cb2d3e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
}

.unfollow{
  background: transparent;
  color: rgba(180, 180, 180, 0.7);
}

.unfollow{
  background: #cb2d3e;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ef473a, #cb2d3e);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ef473a, #cb2d3e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
}